import Vue from 'vue';

import App from './App';
import router from './common/utils/router';

// 
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

// 
import Swiper from './components/other/swiper/index.js';
import './components/other/swiper/style.css';

import DataView from '@jiaminghi/data-view';

//  etc
import GlobalConfig from './common/utils/global-config';

// vuex
import store from './common/store';

// 
import '../static/iconfont/iconfont.css';

Vue.use(ElementUI);

Vue.use(Swiper.default);

Vue.use(DataView);

Vue.use(GlobalConfig);

const sn = require('./common/store/$sn.mixin');
Vue.mixin(sn);

/*
* 
* 1sn-.vue
* 2 name 
* */
const assembly = require.context('./components', true, /sn-.+\.vue$/);
assembly.keys().forEach(el => {
  const single = assembly(el).default;
  Vue.component(single.name, single);
});

window.eventBus = new Vue();

Vue.config.productionTip = false;

const vue = new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
});

export default vue;
